:export {
  primary: #1C3043;
  sub: #bccee3;
  success: #0FDEBD;
  error: #dc221a;
  dark: #000;
  base: #333; }

html {
  font-size: 62.5%; }

body {
  word-break: break-all;
  word-wrap: break-word;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
  line-height: 1.6;
  font-family: "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "メイリオ", Meiryo, sans-serif;
  font-size: 14px;
  font-size: 1.4rem;
  color: #333; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom; }

li {
  list-style-type: none; }

a {
  color: #333;
  text-decoration: underline; }
  a:hover {
    text-decoration: none; }

@media print, screen and (min-width: 50.75em) {
  .pc-hide {
    display: none !important; } }

@media screen and (max-width: 50.6875em) {
  .sp-hide {
    display: none !important; } }

.button {
  padding: 10px 15px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 0;
  width: 200px; }
  .button.wide {
    width: 100%; }
  .button.large {
    width: 400px; }
  .button.small {
    width: 100px; }
  .button.gray {
    background: #828384; }
  .button.blue {
    background: #3e61ff; }
